import React, { Component } from 'react';
import { Spring, Transition, animated } from 'react-spring';
import { TweenMax } from 'gsap';

import { Route, Switch, Link } from 'react-router-dom';

import '../css/item.scss';

class ViewCmp extends React.Component {
    constructor(props){
        super(props);
    }

    componentDidMount(){

        let showSpeed = 500;
        TweenMax.from(this.view, showSpeed/1000, 
            {
                opacity: 0,
                scale: 1.1,
                display: 'block'
            }
        );    
        
        // view page scroll top
        window.scrollTo({'top':0});
    }

    componentWillUnmount(){
        // console.log('will unmount');
    }


    render(){

        return (
            <div className="item_view" ref={view => this.view = view}>

                <div className="inner">
                    <div className="visual">
                        <img src={this.props.img}/>
                    </div>
                    <div className="desc">
                        <p>
                            <strong>{this.props.title}</strong>
                        </p>
                        <p>
                            {this.props.cate}
                        </p>
                        <p>
                            {this.props.info}
                        </p>
                    </div>

                </div>

            </div>
        )
    }
}

export default ViewCmp;