import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { Tween, Timeline } from 'react-gsap';
import { TweenMax } from 'gsap';

class Header extends React.Component { 
    constructor(props){
        super(props);
        this.state = {
            isMenuOpen: false,
        }

        this.nav = null;
        this.menuTween = null;
    }
    
    componentDidMount() {
        this.menuTween = TweenMax.fromTo(this.nav, .4, 
            {
                x:-30,
                opacity:0,
                display:'none'
            },
            {
                x:0,
                opacity:1,
                display:'block'
            },
        );

        // 초기 트윈 셋팅 pause
        this.menuTween.pause();
    }

    menuToggle = () => {
        this.setState({
            isMenuOpen: !this.state.isMenuOpen
        });
        !this.state.isMenuOpen ? this.menuTween.play() : this.menuTween.reverse();
        // console.log(this.state.isMenuOpen);
    }

    render(){
        return (
            <header>
                <h1 className="logo">Logo</h1>
                <a href="#none" className="menu" onClick={this.menuToggle}><span className="hide">Menu</span></a>

                {/* ref */}
                <nav ref={nav => this.nav = nav}>
                    <ul>
                        <li><NavLink exact to={`${process.env.PUBLIC_URL}/`} activeClassName="active">Home</NavLink></li>
                        <li><NavLink to="/Guide" activeClassName="active">Guide</NavLink></li>
                        <li><NavLink to="/Work" activeClassName="active">Work</NavLink></li>
                        <li><NavLink to="/Contact" activeClassName="active">Contact</NavLink></li>

                        {/* <li><NavLink exact to={`${process.env.PUBLIC_URL}/`} activeClassName="active">Home</NavLink></li>
                        <li><NavLink to={`${process.env.PUBLIC_URL}/Guide`} activeClassName="active">Guide</NavLink></li>
                        <li><NavLink to={`${process.env.PUBLIC_URL}/Work`} activeClassName="active">Work</NavLink></li>
                        <li><NavLink to={`${process.env.PUBLIC_URL}/Contact`} activeClassName="active">Contact</NavLink></li> */}
                    </ul>
                </nav>
            </header>
        )
    }

};

export default Header;