import React, { Component } from 'react';

// isotope
import Isotope from 'isotope-layout';


class Home extends Component {


  componentDidMount(){

    // Isotope js
    let grid = document.querySelector('.grid');
    let iso = new Isotope( grid, {
      itemSelector: '.grid-item',
      masonry: {
        columnWidth: 10
      }
    });

    // bind filter click
    let filtersElem = document.querySelector('.filters-button-group');
    let links = filtersElem.querySelectorAll('a');
    
    filtersElem.addEventListener( 'click', function(e) {
      
      let filterValue = e.target.getAttribute('data-filter');
      iso.arrange({ filter: filterValue });
      
      e.target.classList.add('on');

      for (let i = 0; i < links.length; i++) {
        if(links[i] === e.target) continue;
        links[i].classList.remove('on');
      }
    });
    
  }

  render() {

    return (
      <React.Fragment>
          Home Layout


          <div className="filters-button-group">
            <a href="#none" className="on" data-filter="*">All</a>
            <a href="#none" data-filter=".web">Web</a>
            <a href="#none" data-filter=".mobile">Mobile</a>
          </div>

          <div className="grid">
            <div className="grid-item web">
              1
            </div>
            <div className="grid-item mobile">
              2
            </div>
            <div className="grid-item mobile">
              3
            </div>
            <div className="grid-item mobile">
              4
            </div>
            <div className="grid-item mobile">
              5
            </div>
            <div className="grid-item web mobile">
              6
            </div>
            <div className="grid-item web">
              7
            </div>
            <div className="grid-item web">
              8
            </div>
            <div className="grid-item web">
              9
            </div>
            <div className="grid-item web">
              10
            </div>
          </div>
          
      </React.Fragment>
    );

  }
}

export default Home;
