import React, { Component } from 'react';
import ListCmp from '../component/ListCmp';

import Isotope from 'isotope-layout';
import imagesLoaded from 'imagesloaded';

// image import
import img1 from '../images/work/ourwork-aritaum-img.jpg';
import img2 from '../images/work/ourwork-asiana-img.jpg';
import img3 from '../images/work/ourwork-lottehotel-img.jpg';
import img4 from '../images/work/ourwork-lottetower-img.jpg';
import img5 from '../images/work/ourwork-namuh-img.jpg';
import img6 from '../images/work/ourwork-samsunghospital-img.jpg';

import prjList from '../projects/prjList.json';


class Work extends Component {
  constructor(props){
    super(props);
    this.state = {
        listData: [
            {
                title: prjList[1].title,
                info: prjList[1].info,
                cate: prjList[1].cate,
                img:img1,
                link: 'Project1'
            },
            {
                title: prjList[2].title,
                info: prjList[2].info,
                cate: prjList[2].cate,
                img:img2,
                link: 'Project2'
            },
            {
                title: prjList[3].title,
                info: prjList[3].info,
                cate: prjList[3].cate,
                img:img3,
                link: 'Project3'
            },
            {
                title: prjList[4].title,
                info: prjList[4].info,
                cate: prjList[4].cate,
                img:img4,
                link: 'Project4'
            },
            {
                title: prjList[5].title,
                info: prjList[5].info,
                cate: prjList[5].cate,
                img:img5,
                link: 'Project5'
            },
            {
                title: prjList[6].title,
                info: prjList[6].info,
                cate: prjList[6].cate,
                img:img6,
                link: 'Project6'
            },
            {
                title: prjList[1].title,
                info: prjList[1].info,
                cate: prjList[1].cate,
                img:img1,
                link: 'Project1'
            },
            {
                title: prjList[2].title,
                info: prjList[2].info,
                cate: prjList[2].cate,
                img:img2,
                link: 'Project2'
            },
            {
                title: prjList[3].title,
                info: prjList[3].info,
                cate: prjList[3].cate,
                img:img3,
                link: 'Project3'
            },
            {
                title: prjList[4].title,
                info: prjList[4].info,
                cate: prjList[4].cate,
                img:img4,
                link: 'Project4'
            },
            {
                title: prjList[5].title,
                info: prjList[5].info,
                cate: prjList[5].cate,
                img:img5,
                link: 'Project5'
            },
            {
                title: prjList[6].title,
                info: prjList[6].info,
                cate: prjList[6].cate,
                img:img6,
                link: 'Project6'
            },
        ],
        isLoaded: false,
    }

  }


  //Loading ing
  componentDidMount(){

    // isotope안에서 state변경 위함
    // isotope안에서 this가 바뀌므로 this를 _this에 저장해서 사용
    let _this = this;

    // Isotope js
    let grid = document.querySelector('.item_wrap');

    // imagesLoaded / Detect when images have been loaded.
    imagesLoaded(grid, function(){
        console.log('loaded');

        // 로드되는 시간 동안 로딩바 노출
        _this.setState({
            isLoaded: !_this.state.isLoaded
        });

        let iso = new Isotope( grid, {
            itemSelector: '.item_card',
            masonry: {
                columnWidth: 300,
				gutter: 50,
				isFitWidth: true
            }
        });
    
        // bind filter click
        let filtersElem = document.querySelector('.filters-button-group');
        let links = filtersElem.querySelectorAll('a');
        
        filtersElem.addEventListener('click', function(e) {
        
        let filterValue = e.target.getAttribute('data-filter');
        iso.arrange({ filter: filterValue });
        
        e.target.classList.add('on');

        for (let i = 0; i < links.length; i++) {
            if(links[i] === e.target) continue;
            links[i].classList.remove('on');
        }
        });

    });

  }


  //자식컴포넌트 ListCmp 의 상태값을 받아오기
  handleCreate = (childIsOpen) => {
    //   console.log('isOpen은',childIsOpen);
  }

  render(){
      return (
          <React.Fragment>

            {/* loading */}
            {this.state.isLoaded ? 
                ''
            :
                <div className="loading_box">
                   <span>Loading ...</span>
                </div>
            }


            <div className="filters-button-group">
                <a href="#none" className="on" data-filter="*">All</a>
                <a href="#none" data-filter=".web">Web</a>
                <a href="#none" data-filter=".mobile">Mobile</a>
            </div>

            <div className="item_wrap">
                {/* ListCmp 맵핑 */}
                {this.state.listData.map((list,i) => {
                    return (
                        <ListCmp 
                            title={list.title} 
                            info={list.info} 
                            img={list.img} 
                            cate={list.cate}
                            cont={list.cont}
                            link={list.link}
                            idx={i} 
                            key={i} 
                            onCreate={this.handleCreate}
                        />
                    )
                })}
                
            </div>


            {/* 
                자식에서 부모로 전달하기
                ListCmp isOpen 을 어떻게 받아오기
                부모 Cmp : 콜백정의 후 자식에게 props로 전달
                자식 Cmp : this.props.콜백  불러오고 파라미터로 데이터를 전달
                콜백함수 내에서 해당 변수를 사용
            */}
          </React.Fragment>
      )
  }

}

export default Work;
